import React, { useCallback, useEffect, useState, useRef } from "react";
import tgPhoto from "./tgPhoto.svg";
import "./registrationForm.scss";

import {
  getUniversities,
  getFaculties,
  getSpecialities,
  getRegions,
  Courses,
  University,
  Region,
  Faculty,
  Speciality,
} from "../../../../Helpers/api";
import DropDown from "../../../../Components/DropDown";
import Input from "../../../../Components/Input";
import RadioBtnGender from "../../../../Components/RadioBtnGender/RadioBtnGender";
import MultiInput from "../../../../Components/MultiInput/MultiInput";
import Checkbox from "../../../../Components/CheckBox/Checkbox";
import {useTranslation} from "react-i18next";
import useLoad from "../../../../Helpers/useLoad";
import Dropdown2 from "../../../../Components/Dropdown2";
import { CourseDropdown, FacultyDropdown, RegionDropdown, SpecialityDropdown, UniversityDropdown } from "../../../../Components/Dropdown2/custom";
import useSession from "../../../../Helpers/session";
import { Redirect } from "react-router-dom";

type FormProps = {};
type CoursesType = { id: number; name: string };

export default function RegistrationForm() {
  const session = useSession();

  // const response1 = fetch(
  //     "https://server.studsearch.org:2324/v2/register/?name=Vasja&gender=male&about=&universityID=1&facultyID=1&specialityID=1&course=1&hostel=false&telegramPhoto=false&token=MRNWYj5bAPosZyg4v6N3haSSoEYzfppP"
  //   );
  //   response1
  //     .then((response) => response.json())
  //     .then((data: data) => {
  //       console.log("registor", data);
  //     });

  const [region, setRegion] = useState<Region | null>(null);
  const [university, setUniversity] = useState<University | null>(null);
  const [faculty, setFaculty] = useState<Faculty | null>(null);
  const [speciality, setSpeciality] = useState<Speciality | null>(null);
  const [course, setCourse] = useState<CoursesType | null>(null);
  const [nameSurname, setNameSurname] = useState("");
  const [gender, setGender] = useState("male");
  const [aboutMyself, setAboutMyself] = useState("");
  const [checkBoxState, setCheckBoxState] = React.useState({
    tg: true,
    politic: false,
  });


  function handleChange(event: React.ChangeEvent<HTMLInputElement>) {
    const value = event.target.checked;
    setCheckBoxState({
      ...checkBoxState,
      [event.target.name]: value,
    });
  }

  const [error, setError] = React.useState({
    nameSurname: false,
    gender: false,
    region: false,
    university: false,
    faculty: false,
    speciality: false,
    course: false,
  });

  const useFixOnChanged = (key: keyof (typeof error), value: any) =>
    React.useEffect(() => {
      if (value && error?.[key]) setError({...error, [key]: true})
    }, [ value ]);
  useFixOnChanged('nameSurname', nameSurname);
  useFixOnChanged('region', region);
  useFixOnChanged('university', university);
  useFixOnChanged('faculty', faculty);
  useFixOnChanged('speciality', speciality);
  useFixOnChanged('course', course);

  function SubmitStates(event: any) {
    let readyToSubmit = true;
    const dataPost = {
      nameSurname,
      gender,
      region,
      university,
      faculty,
      speciality,
      course,
      aboutMyself,
      tg: checkBoxState.tg,
      politic: checkBoxState.politic,
    };
    Object.entries(dataPost).map(([key, value]) => {
      if (key !== "aboutMyself") {
        if (value == "" || value === undefined || value === null || value == false) {
            event.preventDefault();
            readyToSubmit = false;
          setError((prevUser) => ({ ...prevUser, [key]: true }));
        } else {
          setError((prevUser) => ({ ...prevUser, [key]: false }));
        }
      }
    });
    if (readyToSubmit) {
      console.log("ready To send ");
    }
  }
  const { t, i18n } = useTranslation();

  if (session?.verified)
    return <Redirect to='/' />;

  return (
    <div className={`SignForm`}>
      <form>
        <div className={`flName`}>
          <Input
            value={nameSurname}
            error={error.nameSurname}
            placeholder=" "
            title= {t('registration-name-placeholder')}
            onChange={(changedVal: string) => {
              setNameSurname(changedVal);
            }}
          />
        </div>
        <RadioBtnGender
          gender={gender}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
            setGender(event.target.value);
          }}
        />
        <div className={`regionBlock`}>
          <RegionDropdown
            name={t('registration-region-placeholder')}
            singleBorder
            error={error.region}

            multiple={false}
            value={region}
            onChange={setRegion}
          />
        </div>
        <div className={`universityBlock`}>
          <UniversityDropdown
            name={t('registration-university-placeholder')}
            singleBorder
            error={error.university}

            regions={region ? [ region ] : []}

            multiple={false}
            value={university}
            onChange={setUniversity}
          />
        </div>
        <div className={`facultyBlock`}>
          <FacultyDropdown
            name={t('registration-faculty-placeholder')}
            singleBorder
            error={error.faculty}

            universities={university ? [ university ] : []}

            multiple={false}
            value={faculty}
            onChange={setFaculty}
          />
        </div>
        <div className={`specialityCourseBlock`}>
          <SpecialityDropdown
            name={t('registration-speciality-placeholder')}
            singleBorder
            error={error.speciality}
            universities={university ? [ university ] : []}
            multiple={false}
            value={speciality}
            onChange={setSpeciality}
          />
          <CourseDropdown
            name={t('registration-course-placeholder')}
            error={error.course}
            singleBorder
            multiple={false}
            value={course}
            onChange={setCourse}
          />
        </div>
        <MultiInput
          name={`textValue`}
          value={aboutMyself}
          onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) => {
            setAboutMyself(event.target.value);
          }}
          field={true}
        />
        <div className="checkBoxBlock">
          <Checkbox
            label="tg"
            value={t('registration-checkBox-tg-photo')}
            checked={checkBoxState.tg}
            onChange={handleChange}
          />
          <Checkbox
            label="politic"
            value={t('registration-checkBox-confidential-part-1')}
            tag={<a href={`#`}>{t('registration-checkBox-confidential-part-2')}</a>}
            checked={checkBoxState.politic}
            onChange={handleChange}
          />
        </div>
        <p className={`useTelegram`}>
            {t('registration-checkBox-helper-text')}
        </p>

          <div className="authTelegram">
            <a
              className={`regButton`}
              onClick={SubmitStates}
              href={`https://server.studsearch.org:2324/v2/register/?name=${nameSurname}&gender=male&about=&universityID=${university?.id}&facultyID=${faculty?.id}&specialityID=${speciality?.id}&course=${course?.id}&hostel=false&telegramPhoto=false&token=${session?.token}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <img src={tgPhoto} alt="tgPhoto" />
              <span>{t('registration-confirm-telegram')}</span>
            </a>
           </div>
      </form>
    </div>
  );
}
